<template>
  <div class="p_search_list p_home">
    <div class="tips">
      <div class="go_back" @click="$router.back(-1)">
        <i class="iconfont iconzuo"></i>
        <span class="icon_text">返回</span>
      </div>
      <span class="tips_cont">出现穴位24个，总次数318次</span>
    </div>
    <div class="lists">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div class="list_contain" v-for="(item, index) in list" :key="index">
          <div class="left_title">
            <span class="numBer">{{ index + 1 }}</span>
            <span class="title">肾俞穴</span>
          </div>
          <div class="right_nums">
            <span class="frequency">共出现260次</span>
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import { List } from "vant";
export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
    };
  },
  methods: {
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../../../assets/styles/currency/index.scss";
@import "../../../assets/styles/statistics/index.scss";
</style>


